.django_axe_header {
    position: fixed;
    bottom: 0;
    right: 0;
    flex-direction: row;
    display: flex;
    padding: 0.5rem;
    border: solid #712cf9 1px;
    margin: 1rem;
    border-radius: 50%;
    background: #712cf9;
    text-align: center;
    color: white !important;
    z-index: 9999;

    .links{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    a {
        text-decoration: none !important;
        font-size: 12px;
        color: white !important;
    }
    
}